.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: flex-end; /* Align to the bottom of the screen */
    color: white;
  }
  
  .popup-content {
    background-color: blue;
    padding: 20px;
    border-radius: 20px 20px 0 0;
    width: 100%; /* Full width of the screen */
    max-width: none;
    height: 50vh; /* Cover half the screen */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    animation: slideUp 0.4s ease forwards; /* Apply slideUp animation */
  }
  
  .popup-content p {
    margin: 10px 0; /* Optional: Add some spacing between paragraphs */
  }
  
  @keyframes slideUp {
    0% {
      transform: translateY(100%); /* Start off the screen */
    }
    100% {
      transform: translateY(0); /* Slide into view */
    }
  }
  